/* Reset b치sico */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Body */
body {
  background-color: var(--bg-color);
  color: var(--text-color);
}

/* Vari치veis de tema */
:root {
  --bg-color: #f3f4f6; /* claro */
  --text-color: #111827;
  --chat-bg: #fff;
  --chat-sent: #dcf8c6;
  --chat-received: #e5e5ea;
}

body.dark {
  --bg-color: #1e1e2f;
  --text-color: #e0e0e0;
  --chat-bg: #2c2c3a;
  --chat-sent: #056162;
  --chat-received: #3a3a4c;
}

/* Layout principal */
.chat-layout {
  display: flex;
  height: 100vh;
}

/* Sidebar de usu치rios */
.sidebar {
  width: 300px;
  background-color: var(--chat-bg);
  border-right: 1px solid #ccc;
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.sidebar h2 {
  text-align: center;
  margin-bottom: 10px;
}

.user-list {
  flex: 1;
  overflow-y: auto;
}

.user-item {
  padding: 8px;
  cursor: pointer;
  border-radius: 6px;
}

.user-item:hover {
  background-color: #d1d5db33;
}

.user-item.active {
  background-color: #3b82f633;
}

/* Adicionar usu치rio */
.add-user {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
}

.add-user input {
  margin-bottom: 5px;
  padding: 6px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

/* Chat principal */
.chat-main {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 10px;
  background-color: var(--chat-bg);
}

.chat-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;
  border-bottom: 1px solid #ccc;
}

.chat-box {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.message {
  max-width: 70%;
  padding: 8px 12px;
  border-radius: 20px;
  word-wrap: break-word;
}

.message.sent {
  background-color: var(--chat-sent);
  align-self: flex-end;
}

.message.received {
  background-color: var(--chat-received);
  align-self: flex-start;
}

.chat-input {
  display: flex;
  gap: 8px;
  padding-top: 10px;
  border-top: 1px solid #ccc;
}

.chat-input input {
  flex: 1;
  padding: 8px;
  border-radius: 20px;
  border: 1px solid #ccc;
}

.chat-input button {
  padding: 8px 16px;
  border-radius: 20px;
  background-color: #3b82f6;
  color: #fff;
  border: none;
  cursor: pointer;
}
